import ActionTypes from "../actions";
const initialValue = {
  lists: JSON.parse(localStorage.getItem("lists")) || [],
  amount: 0,
};

const cartReducer = (state = initialValue, action) => {
  // to bin chung  vo trong dng li ko cn khai bo
  let newList = state.lists;
  let amountCart = state.amount;
  // phn loi hnh ng c thc thi
  switch (action.type) {
    case ActionTypes.ADD_CART:
      // kim tra sn phm gi hng xem c trong danh sch hin ti cha.
      // nu c th cp nhp s lng
      // nu cha c th thm mi phn t vo danh sch vi s lng = 1
      const idx = newList.findIndex(
        // phi tho c 2 k  tm ng CH MC ca sn phm ta cn id v size, dng findIndex  tm 1 ch mc( l 1 object) trong 1 mng l hp l nht
        (currentValue) =>
          currentValue.id === action.detail.id &&
          currentValue.size === action.size
      );
      // trong variant c rt nhiu size ta phi tm ng size m action ta gi qua, dng find n s tm BN TRONG 1 i tng
      const variantSize = action.detail.variant.find(
        (currentValue) => currentValue.size === action.size
      );
      // nu m idx < 0 th thm mi v d liu thm mi c ly t action qua, xong ri ta push vo mng newList
      // ngc li th update quantity  update dc ta phi tm ng ci ch mc cn update l th no (gm id v size)
      if (idx < 0) {
        const productCart = {
          id: action.detail.id,
          avatar: action.detail.avatar,
          name: action.detail.name,
          nameProduct: action.detail.nameProduct,
          variant: variantSize,
          price: action.detail.price,
          size: action.size,
          quantity: 1,
        };
        newList.push(productCart);
      } else {
        newList[idx].quantity++;
      }
      // set li v lu vo localStorage
      localStorage.setItem("lists", JSON.stringify(newList));
      // amountCart ta  bng 0 trong mi hnh ng  + chnh xc gi tr trong 1 sn phm, nu ta ko  th n s ly ci amount trong ci tr khi to  n cng dn => hin th ko ng
      amountCart = 0;
      // duyt tng phn t ca tng ch mc ly ra ci quantity c nh th n ch mc cui cng n ra c tng s quantity v gn cho amountCart  bit tng s lng  chn bao nhiu sn phm
      newList.forEach((element) => {
        amountCart += element.quantity;
      });
      // thc hin xong hnh ng n s dc truyn xung  render li gi tr mi
      return {
        ...state,
        lists: newList,
        amount: amountCart,
      };
    case ActionTypes.UPDATE_CART:
      // tm sn phm trong danh sch cp nhp li s lng
      // tm bn trong newList v tm ng ci id v size, phi tho 2 k l size v id trong newList c bng vi size v id t action truyn vo ko nu bng th tm c i tng cn update
      const updateItem = newList.find(
        (currentValue) =>
          currentValue.size === action.detail.size &&
          currentValue.id === action.detail.id
      );
      // sau khi tm ng ri ta truy cp n quantity  cp nhp li s lng
      // dng ton t bt 3  k.tr xem c c truyn hay ko nu c th dc gn g.tr mi cn ko truyn th bng 0, khc phc c tnh trng ngi dng ko nhp g.tr
      updateItem.quantity = action.quantity ? Number(action.quantity) : 0;
      // lu li gi tr mi vo localStorage
      localStorage.setItem("lists", JSON.stringify(newList));
      amountCart = 0;
      newList.forEach((element) => {
        amountCart += element.quantity;
      });
      return {
        ...state,
        lists: newList,
        amount: amountCart,
      };
    case ActionTypes.REMOVE_CART:
      // tm sn phm  trong danh sch v xo n ra khi danh sch
      // tm ng CH MC c id v size cn xo
      const deleteItemIndex = newList.findIndex(
        (currentValue) =>
          currentValue.id === action.detail.id &&
          currentValue.size === action.detail.size
      );
      // nu c ch mc(c sn phm trong gi hng) th ta tin hnh ct phn t  ra khi mng
      if (deleteItemIndex >= 0) {
        newList.splice(deleteItemIndex, 1);
      }
      // lu li gi tr mi vo localStorage
      localStorage.setItem("lists", JSON.stringify(newList));
      // v sao delete cng phi cn forEach v khi xo n  mt i 1 sn phm ta phi cn cp nhp li tng s quantity  render cho chnh xc
      amountCart = 0;
      newList.forEach((element) => {
        amountCart += element.quantity;
      });
      return {
        ...state,
        lists: newList,
        amount: amountCart,
      };
    case ActionTypes.CHECK_OUT:
      // remove localStorage
      localStorage.removeItem("lists");
      // ta render li nh g.tr khi to ban u
      return {
        ...state,
        lists: [],
        amount: 0,
      };
    default:
      return { ...state };
  }
};

export default cartReducer;
