import React, { useState, useEffect } from "react";
import settingService from "../services/settingService";
import detailService from "./../services/detailService";
import { Link } from "react-router-dom";
import api from "./../services/api";
import NumberFormat from "react-number-format";

const Home = () => {
  const [carousels, setCarousels] = useState([]);
  const [posters, setPosters] = useState([]);
  // hotProducts lc mnh i y n ch tr v ci items v details ko c dc ci lists
  const [hotProducts, setHotProducts] = useState([]);
  // thm hotProductsId lm 1 ci mng song song vi th hotProducts th mnh s ly dc listId
  const [hotProductsId, setHotProductsId] = useState([]);
  const [homeProducts, setHomeProducts] = useState([]);
  const [homeProductsId, setHomeProductsId] = useState([]);

  useEffect(() => {
    // get(1) l ch ly ng api c id l 1, res.value.items (cho carousel)c ngha l lc to value l 1 object trong  c items l 1 ci mng chui cho nn l mng phi dng usf dng mng  gi gi tr, nu mc ko c id th ta dng key={idx} thay cho key={lorem.id}, vd nh TH ny
    settingService.get(1).then((res) => setCarousels(res.value.items));

    // get(2) l ch ly ng api c id l 2 res.value.items (cho Poster)
    settingService.get(2).then((res) => setPosters(res.value.items));

    // get(3) l ch ly ng api c id l 3 res.value.items (cho hotProduct)
    // nu ch c settingService.get(3) ny thi th n ch mi c 3 g.tr l listId, itemId, detailId thi cho nn l ko th setProducts lin dc, vy lm th no  tham chiu 3 g.tr ny n c 1 trang hnh detail dc? m ch nm trong ni b settingService.get(3) ny thi
    settingService.get(3).then((res) => {
      // do th hotProducts trn ng i n ch ly dc itemId v detailId, thi nn ta phi dng HotProductsId  lm g, n ko phi trn ng i nn n lun c 3 g.tr l listId, itemId, detailId, v th ta dng thng ny  dn ti trang detail, v sao gi l mng song song th c ngha l thng hotProduct c listId l 1 th thng hotProductsId ny c listId cng l 1
      setHotProductsId(res.value.items);
      // to trc 1 ci mng
      const apiHotArray = [];
      // lp tng phn t v thm vo mng, element c th hiu l res.value.items. forEach ch dc dng  duyt mng or tp hp (a s l mng), v s ln lp = vi s p.t c trong mng, c th dng for of
      res.value.items.forEach((element) => {
        apiHotArray.push(
          // thm nguyn ci api ny vo mng, v ci api ny n chnh l a ch dn ti trang detail
          detailService.get(element.listId, element.itemId, element.detailId)
        );
      });
      // sau khi c  ht phn t ri mi a vo promise,  ch  ht ri mi setHotProducts  n render cng 1 lc.
      api.promise(apiHotArray).then(
        api.spread((...res) => {
          setHotProducts(res);
        })
      );
    });

    // get(4) l ch ly ng api c id l 4 res.value.items (cho homeProduct)
    settingService.get(4).then((res) => {
      setHomeProductsId(res.value.items);
      const apiHomeArray = [];
      res.value.items.forEach((element) => {
        apiHomeArray.push(
          detailService.get(element.listId, element.itemId, element.detailId)
        );
      });

      api.promise(apiHomeArray).then(
        api.spread((...res) => {
          setHomeProducts(res);
        })
      );
    });
  }, []);

  return (
    <main>
      <div className="container">
        {/*Loop Poster */}
        {posters.map((poster, idx) => (
          <div className="text-center mb-4 mb-xl-5" key={idx}>
            {/* nu m c poster.title th thm th h3 ko th  trng */}
            {poster.title ? <h3>{poster.title}</h3> : ""}
            <div>
              <img
                className="img-fluid"
                src={poster.image}
                alt={poster.description}
              />
            </div>
          </div>
        ))}

        {/* Loop Carousel*/}
        <div className="text-center mb-4 mb-xl-5">
          <h3>GU thi trang c tnh, c o</h3>
          <div
            id="carouselExampleInterval"
            className="carousel slide"
            data-bs-ride="carousel"
          >
            <div className="carousel-inner">
              {carousels.map((carousel, idx) => (
                <div
                  // active ch p dng ng th c idx = 0
                  className={`carousel-item ${idx === 0 ? "active" : ""}`}
                  data-bs-interval={2000}
                  key={idx}
                >
                  {/* bn trong carosels n l 1 chui gm cc hnh nh */}
                  <img className="d-block img-fluid" src={carousel} alt={""} />
                </div>
              ))}

              <button
                className="carousel-control-prev opacity-0"
                type="button"
                data-bs-target="#carouselExampleInterval"
                data-bs-slide="prev"
              >
                <span
                  className="carousel-control-prev-icon"
                  aria-hidden="true"
                />
                <span className="visually-hidden">Previous</span>
              </button>
              <button
                className="carousel-control-next opacity-0"
                type="button"
                data-bs-target="#carouselExampleInterval"
                data-bs-slide="next"
              >
                <span
                  className="carousel-control-next-icon"
                  aria-hidden="true"
                />
                <span className="visually-hidden">Next</span>
              </button>
            </div>
          </div>
        </div>
        {/* demo sn phm hot*/}
        <div className="text-center mb-4 mb-xl-5">
          <h3>Top sn phm hot</h3>
          <p>Nhng sn phm thi trang mi nht/Hot nht</p>
          <div className="row row-cols-2 row-cols-lg-4 g-3">
            {hotProducts.map((hotProduct, idx) => (
              <div className="col text-center" key={hotProduct.id}>
                {/* vn  kh  y l lm th no  ly dc ci listId  n i n trang detail,  gii thch  trn v sao phi dng hotProductsId v lc ny phi thm idx vo v sao v th hotProducts n c id nn ko cn idx nhng trn ng i n ko ly dc listId, nn buc phi dng hotProductsId m thng ny li ko c id, m n c listId cho nn l phi dng idx  lm d liu song song */}
                <Link
                  to={`/san-pham/${hotProductsId[idx].listId}/${hotProductsId[idx].itemId}/${hotProductsId[idx].detailId}`}
                >
                  <div className="body-image">
                    <img
                      src={hotProduct.avatar}
                      alt={hotProduct.name}
                      className="image-visible img-fluid"
                    />
                    <img
                      src={hotProduct.avatar1}
                      alt={hotProduct.name}
                      className="image-hidden img-fluid"
                    />
                  </div>
                </Link>
                <div>
                  <NumberFormat
                    value={hotProduct.price}
                    displayType={"text"}
                    thousandSeparator={true}
                    suffix={" "}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* demo sn phm ti nh */}
        <div className="text-center">
          <h3>GU thi trang  nh</h3>
          <p>Nhng sn phm thi trang ph bin ti nh</p>
          <div className="row row-cols-2 row-cols-lg-4 g-3">
            {homeProducts.map((homeProduct, idx) => (
              <div className="col text-center" key={homeProduct.id}>
                <Link
                  to={`/san-pham/${homeProductsId[idx].listId}/${homeProductsId[idx].itemId}/${homeProductsId[idx].detailId}`}
                >
                  <div className="body-image">
                    <img
                      src={homeProduct.avatar}
                      alt={homeProduct.name}
                      className="image-visible img-fluid"
                    />
                    <img
                      src={homeProduct.avatar1}
                      alt={homeProduct.name}
                      className="image-hidden img-fluid"
                    />
                  </div>
                </Link>
                <div>
                  <NumberFormat
                    value={homeProduct.price}
                    displayType={"text"}
                    thousandSeparator={true}
                    suffix={" "}
                  />{" "}
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </main>
  );
};
export default Home;
