import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Input from "../components/Input";
import { useDispatch, useSelector } from "react-redux";
import ActionTypes from "../store/actions";
import NumberFormat from "react-number-format";
import { Modal, Button, Table } from "react-bootstrap";

const Cart = () => {
  const dispatch = useDispatch();
  const chooseLists = useSelector((state) => state.auth.lists);
  const [forceUpdate, setForceUpdate] = useState(false);
  const [totalCart, setTotalCart] = useState(0);
  const [infoName, setInfoName] = useState("");
  const [infoPhone, setInfoPhone] = useState("");
  const [infoAddress, setInfoAddress] = useState("");
  const [infoNote, setInfoNote] = useState("");
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  // khi no cn uef l khi m n render xong th mi gi uef
  useEffect(() => {
    //v sao  setTotalCart trong uef l v ta mun n tnh tng gi tin cho tng sn phm xong ht ri (l render xong xui ht) mi bt u vic tnh tng
    setTotalCart(sum());
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // TH ch s dng 1 ln th ta truyn trc tip ci mng vo trong hm lun
  function sum() {
    // trong 1 function ch c duy nht 1 lnh return
    let total = 0;
    for (let index = 0; index < chooseLists.length; index++) {
      let ele = chooseLists[index].price * chooseLists[index].quantity;
      total += ele;
    }
    return total;
  }

  const updateOneProductAction = (e, item) => {
    dispatch({
      type: ActionTypes.UPDATE_CART,
      detail: item,
      quantity: e.target.value,
      // e.target.value ny l focus vo input  ly gi tr, cch test l console.log(e.target.value) xem n ly dc ng g.tr ca input hay ko
    });
    // u tin ta cho n l false khi m hnh ng update dc thc thi th n s set li cho mnh l true v n s render li th n s hin th li gi tin i km theo, tng t cho hnh ng xo cng vy
    setForceUpdate(!forceUpdate);
    // ta setTotalCart li mc ch l khi c s update hay delete th n render li gi tr tng
    setTotalCart(sum());
  };

  const deleteOneProductAction = (e, item) => {
    if (e) e.preventDefault();
    dispatch({
      type: ActionTypes.REMOVE_CART,
      detail: item,
    });
    setForceUpdate(!forceUpdate);
    setTotalCart(sum());
  };

  const removeAllProductAction = () => {
    dispatch({
      type: ActionTypes.CHECK_OUT,
    });
    handleClose();
    setTotalCart(0);
  };

  const getInfoName = (e) => {
    setInfoName(e.target.value);
  };
  const getInfoPhone = (e) => {
    setInfoPhone(e.target.value);
  };
  const getInfoAddress = (e) => {
    setInfoAddress(e.target.value);
  };
  const getInfoNote = (e) => {
    setInfoNote(e.target.value);
  };
  return (
    <>
      <main>
        <div className="container">
          <div className="body-bar mb-3">
            <Link to="/home">
              <i className="bi bi-house-fill" />
            </Link>
            <span> Thng tin gi hng ca bn</span>
          </div>
          {/* if else */}
          <div className="row row-cols-md-2 gx-4">
            <div className="col-md mb-3 mb-lg-0">
              <h4>Chi tit n hng</h4>

              {chooseLists.map((item, idx) => (
                <div className="row border-top border-bottom py-3" key={idx}>
                  <div className="col-3 text-center">
                    <img
                      src={item.avatar}
                      alt={item.name}
                      className="img-fluid d-block"
                    />
                    <a
                      href="/cart"
                      className="btn btn-outline-danger"
                      onClick={(e) => deleteOneProductAction(e, item)}
                    >
                      <i className="bi bi-trash" /> Xo
                    </a>
                  </div>
                  <div className="col-9">
                    <div className="border-bottom mb-2">
                      <p>{item.nameProduct}</p>
                      <p>
                        {item.variant.color}, {item.variant.size}
                      </p>
                    </div>
                    <div>
                      <form>
                        <div className="d-flex my-3">
                          <input
                            className="form-control me-2"
                            defaultValue={item.quantity}
                            // onChange ta truyn hm vi tham s e  ly ci this ca input, item ny bn trong n s c id v size, ko dc dng gi hm khi nm trong 1 map
                            onChange={(e) => updateOneProductAction(e, item)}
                          />
                        </div>
                        <span>
                          =
                          <NumberFormat
                            value={item.price * item.quantity}
                            displayType={"text"}
                            thousandSeparator={true}
                            suffix={" "}
                          />
                        </span>
                      </form>
                    </div>
                  </div>
                </div>
              ))}
              <div className="offset-3 mt-1 mb-3">
                Tng:{" "}
                <NumberFormat
                  value={totalCart}
                  displayType={"text"}
                  thousandSeparator={true}
                  suffix={" "}
                />
              </div>

              <a href="/new" className="btn btn-warning w-100">
                Cn sn phm khc? Tip tc mua hng
              </a>
            </div>
            <div className="col-md">
              <h4>Ngi mua/nhn hng</h4>
              <form>
                <Input
                  type="text"
                  id="txtName"
                  label="Tn: "
                  labelSize={12}
                  placeholder="Tn ngi nhn"
                  onChange={(e) => getInfoName(e)}
                />
                <Input
                  type="tel"
                  id="txtPhone"
                  label="in thoi lin lc: "
                  labelSize={12}
                  placeholder="S in thoi"
                  onChange={(e) => getInfoPhone(e)}
                />
                <Input
                  type="text"
                  id="txtAddress"
                  label="a ch nhn hng: "
                  labelSize={12}
                  placeholder="Ni nhn hng"
                  onChange={(e) => getInfoAddress(e)}
                />
                <Input
                  type="text"
                  id="Textarea"
                  label="Ghi ch: "
                  labelSize={12}
                  placeholder="Ni dung ghi ch"
                  rows={3}
                  onChange={(e) => getInfoNote(e)}
                />
                <button
                  type="button"
                  className="btn btn-primary w-100"
                  onClick={handleShow}
                >
                  t hng
                </button>
              </form>
            </div>
          </div>
          {/* Modal */}
          <Modal show={show} onHide={handleClose} size="lg">
            <Modal.Header closeButton>
              <Modal.Title>Thng tin n hng</Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <div className="mb-3">
                - Cm n
                <span>
                  {infoName ? " Anh/Ch: " : " qu khch "}
                  {infoName}{" "}
                </span>
                 la chn mua sn phm ti shop YATB,
                <span>
                  {infoName ? " Anh/Ch: " : " qu khch "}
                  {infoName}{" "}
                </span>
                vui lng kim tra li thng tin sn phm, a ch giao hng v
                n xc nhn  YATB giao hng sm nht c th. Xin cm n v hn
                gp li.
                <div>
                  {infoPhone ? "- S in thoi: " : ""}
                  {infoPhone}
                </div>
                <div>
                  {infoAddress ? "- a ch nhn hng: " : ""}
                  {infoAddress}
                </div>
                <div>
                  {infoNote ? "- Ghi ch: " : ""}
                  {infoNote}
                </div>
              </div>

              <Table bordered hover>
                <thead className="text-center">
                  <tr>
                    <th>STT</th>
                    <th>M t</th>
                    <th>Tn sn phm</th>
                    <th style={{ width: 100 }}>Size</th>
                    <th style={{ width: 100 }}>S lng</th>
                  </tr>
                </thead>
                <tbody>
                  {chooseLists.map((info, idx) => (
                    <tr className="text-center" key={info.id}>
                      <td>{idx + 1}</td>
                      <td>
                        <img src={info.avatar} alt="" className="img-fluid" />
                      </td>
                      <td className="text-center">{info.nameProduct}</td>
                      <td>{info.size}</td>
                      <td>{info.quantity}</td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            </Modal.Body>
            <Modal.Footer>
              <Button variant="primary" onClick={removeAllProductAction}>
                Xc nhn
              </Button>
            </Modal.Footer>
          </Modal>
        </div>
      </main>
    </>
  );
};
export default Cart;
