import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import detailService from "./../services/detailService";
import api from "./../services/api";
import NumberFormat from "react-number-format";

const New = () => {
  const [newProducts, setNewProducts] = useState([]);

  useEffect(() => {
    // JSON.stringtify bin i tng thnh chui
    // JSON.parse bin chui thnh i tng
    //  v bn thn ca productCategory n l mng do d liu trn API l mng nu n cha lu d liu kp th n chn mng trng x l
    const listArr = JSON.parse(localStorage.getItem("productCategory")) || [];
    // mc tiu l ly c danh sch items t th listArr ny ra (dng map() tr v cho mnh 1 ci danh sch mng 2 chiu l mng lng mng) (cn dng flatMap() n tr v cho mnh mng 1 chiu v cha all cc mc sn phm ca children)
    const itemList = listArr.flatMap((item) => item.children);
    // ly 10 phn t trong ci itemList
    const topItem = itemList.slice(0, 10);
    // to 1 ci mng  lu gi tr mi (l danh sch cc details)
    const apiArray = [];
    // ta duyt tng phn t mng bng forEach t topItem v push vo mng apiArray t ci api
    topItem.forEach((element) => {
      apiArray.push(detailService.list(element.listId, element.id));
    });
    // console.log(topItem);

    api.promise(apiArray).then(
      api.spread((...res) => {
        let detailList = [];
        // const one = async () => {}
        // const two = async () => {}
        // const three = async () => {}
        // c ngha l thc hin trnh t bt ng b, lm async 1 trc xong ri mi n 2 xong 2 mi n 3
        const one = async () => {
          // mc tiu l ly dc grandChildren
          // res.flatMap() l response t ci apiArray v flatMap thnh mng 1 chiu
          // vic ta gn bng detailList l g nu m d liu v cha kp th n s ly mng trng  show trnh tnh trang li, ngc li nu m d liu v ri th n s dc gn li gi tr mi, ta thy g dng let n c phm vi khi nhng ang dng async cho nn l phm vi khi lc ny ko c tc dng
          detailList = res.flatMap((item) => {
            // duyt 1 phn t cha v ch ly ra phn t con v duyt tip
            return item.grandChildren.map((x) => {
              // x ang nhn l 1 grandChildren (l 1 detail), do n cha c ci listId nn ko  tham s ko th x ci detail ra dc cho nn l ta thm 1 thuc tnh mi l x.listId v dc gn bng gi tr ly t res.flapMap (item) l ch ly ra ci listId  gn
              x.listId = item.listId;
              return x;
            });
          });
          // console.log("1");
        };
        const two = async () => {
          // console.log("2");
          detailList.sort((a, b) => {
            // do n l dng chui nn ko th sp xp = cch b - a hay a - b, m phi so snh 2 chui vi nhau bng localCompare
            return b.createdDate.localeCompare(a.createdDate);
          });
        };
        const three = async () => {
          // console.log("3");
          // ly 20 sn phm u t detailList  render
          setNewProducts(detailList.slice(0, 20));
          // console.log(detailList);
        };

        one().then(two).then(three);
      })
    );
  }, []);

  return (
    <>
      <main>
        <div className="container">
          <div className="body-bar d-flex align-content-center">
            <Link to="/home">
              <i className="bi bi-house-fill" />
            </Link>
            <span> NEW </span>
          </div>
          <div className="row row-cols-2 row-cols-lg-4 g-3 mt-5">
            {newProducts.map((newProduct, idx) => (
              <div className="col text-center" key={newProduct.id}>
                <Link
                  to={`/san-pham/${newProduct.listId}/${newProduct.itemId}/${newProduct.id}`}
                >
                  <div className="body-image">
                    <img
                      src={newProduct.avatar}
                      alt={newProduct.name}
                      className="image-visible img-fluid"
                    />
                    <img
                      src={newProduct.avatar1}
                      alt={newProduct.name}
                      className="image-hidden img-fluid"
                    />
                  </div>
                </Link>
                <div>
                  {" "}
                  <NumberFormat
                    value={newProduct.price}
                    displayType={"text"}
                    thousandSeparator={true}
                    suffix={" "}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
      </main>
    </>
  );
};
export default New;
